@import url(https://fonts.googleapis.com/css?family=Merriweather&display=swap);
/* Font import from Google Fonts */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --defaultFont: "Merriweather";
}

* {
  box-sizing: border-box;
}

*::-webkit-scrollbar {
  display: none;
}

#root {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: url(/portfolio/static/media/background.55f2a440.jpg);
  background-size: cover;
  background-position: center;
}

#Navbar-container {
  height: 5%;
  width: 85%;
  display: flex;
  justify-content: space-evenly;
  align-items: flex-end;
}

.Navbar-selection {
  padding: 0 0 5px 0;
}

.Navbar-selection a {
  text-decoration: none;
  color: rgb(255, 255, 255);
  font-family: var(--defaultFont);
  font-size: 20px;
  text-shadow: 1px 1px 0 black;
}

.Navbar-selection:hover {
  cursor: pointer;
  padding: 0;
  border-bottom: 5px solid rgb(160, 0, 0);
}

.selected {
  padding: 0;
  border-bottom: 5px solid rgb(160, 0, 0);
}

/* Larger Smartphone Compatibility */
@media only screen and (min-device-width: 376px) and (max-device-width: 812px) and (-webkit-min-device-pixel-ratio: 3) {
  .Navbar-selection a {
    font-size: 18px;
  }
}

/* Smaller Smartphone Compatibility */
@media only screen and (min-device-width: 0px) and (max-device-width: 375px) and (-webkit-min-device-pixel-ratio: 2) {
  .Navbar-selection a {
    font-size: 14px;
  }
}

.Portcard {
  width: 40%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
}
.Portcard-image {
  width: 100%;
  height: 200px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 5px;
  box-shadow: 0 0 5px black;
}

.Portcard a {
  color: white;
  text-shadow: 1px 1px black;
  font-size: 20px;
  font-family: var(--defaultFont);
  margin: 5px;
  font-weight: bold;
}

.Portcard a:hover {
  cursor: pointer;
}

/* Larger Smartphone Compatibility */
@media only screen and (min-device-width: 375px) and (max-device-width: 812px) and (-webkit-min-device-pixel-ratio: 3) {
  .Portcard {
    justify-content: flex-start;
    width: 48%;
  }

  .Portcard-image {
    height: 100px;
  }
  .Portcard a {
    font-size: 14px;
    text-align: center;
  }
}

/* Smaller Smartphone Compatibility */
@media only screen and (min-device-width: 321px) and (max-device-width: 375px) and (-webkit-min-device-pixel-ratio: 2) {
  .Portcard {
    justify-content: flex-start;
    width: 48%;
  }

  .Portcard-image {
    height: 90px;
  }
  .Portcard a {
    font-size: 14px;
    text-align: center;
  }
}

/* Even Smaller Smartphone Capability */
@media only screen and (min-device-width: 0px) and (max-device-width: 320px) and (-webkit-min-device-pixel-ratio: 2) {
  .Portcard {
    justify-content: flex-start;
    width: 48%;
  }

  .Portcard-image {
    height: 75px;
  }
  .Portcard a {
    font-size: 14px;
    text-align: center;
  }
}

#Home-container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: scroll;
}

#Home-picContainer {
  width: 85%;
  padding-bottom: 10px;
  margin-bottom: 10px;
  border-bottom: 2px solid white;
  display: flex;
  justify-content: center;
}

#Home-picture {
  height: 275px;
  width: 275px;
  background-image: url(/portfolio/static/media/me.c8854588.jpg);
  background-size: cover;
  background-position: center;
  border: 2px solid white;
  border-radius: 25px;
  box-shadow: 0 0 6px black;
}

#Home-aboutMe {
  width: 80%;
  text-align: center;
  font-family: var(--defaultFont);
  font-size: 18px;
}

/* Larger Smartphone Compatibility */
@media only screen and (min-device-width: 376px) and (max-device-width: 812px) and (-webkit-min-device-pixel-ratio: 3) {
  #Home-aboutMe {
    font-size: 17px;
  }
}

/* Smaller Smartphone Compatibility */
@media only screen and (min-device-width: 321px) and (max-device-width: 375px) and (-webkit-min-device-pixel-ratio: 2) {
  #Home-container {
    justify-content: flex-start;
  }
  #Home-picture {
    margin-top: 10px;
  }

  #Home-aboutMe {
    font-size: 14px;
  }
}

/* Even Smaller Smartphone Capability */
@media only screen and (min-device-width: 0px) and (max-device-width: 320px) and (-webkit-min-device-pixel-ratio: 2) {
  #Home-container {
    justify-content: flex-start;
  }

  #Home-picture {
    height: 225px;
    width: 225px;
    margin-top: 10px;
  }
}

.Resume-tabSpace {
