}

.buttonContainer {
  width: 100%;
  margin: 5px;
  display: flex;
  justify-content: center;
}

#reduxLogo {
  width: 100%;
  height: 100%;
  background-image: url(/portfolio/static/media/redux.cdb6953a.png);
  background-size: contain;
  background-position: center;
}

h4 {
  margin: 3px;
}

/* Larger Smartphone Compatibility */
@media only screen and (min-device-width: 376px) and (max-device-width: 812px) and (-webkit-min-device-pixel-ratio: 3) {
  #App-container div.ui.grid {
    flex-direction: column;
    align-items: center;
  }

  #App-container div.four.wide.column {
    width: 100% !important;
    padding-bottom: 0;
  }
  #Resume-container
    > div
    > div
    > div.four.wide.column
    > div.ui.fluid.vertical.menu {
    display: flex;
    flex-direction: row;
  }

  #Resume-container
    > div
    > div
    > div.four.wide.column
    > div.ui.fluid.vertical.menu
    > a.item {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 33%;
    text-align: center;
  }

  #Resume-container > div > div > div.stretched.twelve.wide.column {
    width: 100% !important;
  }

  #Resume-container > div > div > div.stretched.twelve.wide.column > div {
    border-radius: 5px;
  }

  #Resume-container > div > div > div.stretched.twelve.wide.column > div ul {
    margin: 0;
    padding: 0px 0px 10px 30px;
  }

  h3.sectionHeader {
    font-size: 16px;
  }

  #Resume-container h4 {
    font-size: 14px;
  }
}

