}

#Contact-phone {
  font-family: var(--defaultFont);
  font-size: 25px;
}

#Contact-social {
  width: 50%;
  display: flex;
  justify-content: center;
  border-bottom: 2px solid white;
  padding-bottom: 10px;
}

#Contact-email {
  font-family: var(--defaultFont);
  font-size: 25px;
}

#Contact-email a {
  color: white;
  text-decoration: underline;
}

#Contact-email a:hover {
  color: maroon;
  text-decoration: underline;
}

.socialIcon {
  color: white;
}

.socialIcon:hover {
  color: maroon;
}

#Contact-container p {
  margin: 10px;
  padding: 0;
  text-align: center;
}

/* Larger Smartphone Compatibility */
